body {
  font-family: 'Open Sans', sans-serif;
}

.flex {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.flex-column {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.nav__links {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 80vw;
  list-style: none;
  color: black;
  padding: 0;
}

.nav__links li a, .home__text a{
  display: inline-block;
  text-decoration: none;
  transition: border-bottom 0.1s;
}

.home__text a {
  font-weight: bold;
  color: #4B9CD3;
}

.home__text img {
  height: 70vh;
}


.nav__links li a:after, .home__text a:after {
  display: block;
  content: '';
  border-bottom: solid 3px #4B9CD3;  
  -webkit-transform: scaleX(0);  
          transform: scaleX(0);  
  transition: -webkit-transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out; 
  -webkit-transform-origin:  0% 50%; 
          transform-origin:  0% 50%;
}

.nav__links li a:hover:after, .home__text a:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1); 
}

.home__text {
  max-width: 80vw;
}


.group-charts, .overview-chart {
  width: 80vw;
}


.unc circle {
  fill: #4B9CD3;
}

.duke circle {
  fill: #012169;
}

.wakemed circle {
  fill: crimson;
}

.tooltip {
  width: 275px;
  height:150px;
  background: white;
  box-shadow: 2px 2px 3px rgba(0,0,0,0.45);
  position: absolute;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  cursor: pointer;
}

.tooltip-content .header{
  font-size: 0.7rem;
  color: #666;
}

.right-justified {
text-align: right;
}

.center-justified {
  text-align: center;
}

.tooltip-content .value{
  text-transform: capitalize;
}

.flex-row {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-flow: row nowrap;
          flex-flow: row nowrap;
  margin-bottom: 1rem;
}

.tooltip-tail {
  width: 1px;
  border-top: 15px solid white;
  border-bottom: 15px solid transparent;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  position: absolute;
  top: 150px;
  left: 120px;
}
